









































































































































































































































































































































































$border-radius: 8px;

body {
  display: grid;
  place-items: center;
  height: 80vh;
  margin: 0;
  background: #bbccd8;
// background-image: url("../images/b.webp");
  main {
    margin: 1rem;
    max-width: 750px;
    width: calc(100% - 2rem);
  }

  h1,
  h2,
  h3 {
    font-family: "Inconsolata", monospace;
  }

  p {
    font-family: "Montserrat", sans-serif;
  }

  pre {
    width: 100%;
    padding: 0;
    margin-top: 1em;
    overflow: auto;
    overflow-y: hidden;

    code {
      padding: 10px;
      color: #333;
      margin: 5px;
    }
  }

  .vue-command {
    -webkit-border-bottom-left-radius: $border-radius;
    -webkit-border-bottom-right-radius: $border-radius;
    -moz-border-bottom-left-radius: $border-radius;
    -moz-border-bottom-right-radius: $border-radius;
    border-bottom-left-radius: $border-radius;
    border-bottom-right-radius: $border-radius;

    ::-webkit-scrollbar {
      width: 6px;
    }

    ::-webkit-scrollbar-track {
      background: #252525;
    }

    ::-webkit-scrollbar-thumb {
      background: #ddbdbd;
    }

    ::-webkit-scrollbar-thumb:hover {
      background: #333;
    }

    .term-bar {
      -webkit-border-top-left-radius: $border-radius;
      -webkit-border-top-right-radius: $border-radius;
      -moz-border-top-right-radius: $border-radius;
      -moz-border-top-left-radius: $border-radius;
      border-top-left-radius: $border-radius;
      border-top-right-radius: $border-radius;
    }

    .term-std {
      min-height: 350px;
      max-height: 350px;
      overflow-y: scroll;
    }
    .term-stdout{
      font-size: 13px;
    }
    .ab{
      text-decoration: none;
      color: #d980b5;
    }
    .cd{
      color: #ef8e93;
    }
    .ef{
        text-decoration: none;
      color: #a7bd83;
    }
    .profile{
      border-radius: 55px;
    }
  }
}
